import PermissionButton from "../../plugin/components/PermissionButton";
import InfoTable from "../../plugin/components/InfoTable";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PageInfo from "../../plugin/script/common/PageInfo";
import InputArea from "../../plugin/components/InputArea";
import tierHandler from "./../../script/handlers/tierHandler";
import InfoTableItem from "../../plugin/components/InfoTableItem";
import storage from "./../../script/storage/storage";
const select = ["添加", "编辑", "删除"];
export default {
  name: "Admin",
  extends: BaseIframe,
  components: { InputArea, PermissionButton, InfoTable, InfoTableItem },
  data() {
    return {
      checkAll: false,
      operation: select,
      isIndeterminate: true,
      //表格数据
      tableStyle: [
        { label: "序号", prop: "serial_number", width: "" },
        { label: "角色名称", prop: "role_name", width: "" },
        { label: "角色描述", prop: "role_state", width: "" },
        { label: "创建时间", prop: "create_time", width: "" },
        { label: "更新时间", prop: "update_time", width: "" },
        { label: "状态", prop: "status", width: "" },
        { label: "操作", prop: "action", width: "" }
      ],
      records: [
        {
          serial_number: "1",
          role_name: "超级管理员",
          role_state: "无法删除",
          create_time: "2020-02-02 12:00:00",
          update_time: "2020-03-01 10:00:00",
          status: "0",
          action: [
            { label: "编辑", type: "edit" },
            { label: "权限", type: "admin" },
            { label: "删除", type: "delete" }
          ]
        },
        {
          serial_number: "1",
          role_name: "超级管理员",
          role_state: "无法删除",
          create_time: "2020-02-02 12:00:00",
          update_time: "2020-03-01 10:00:00",
          status: "1",
          action: [
            { label: "编辑", type: "edit" },
            { label: "权限", type: "admin" },
            { label: "删除", type: "delete" }
          ]
        }
      ],
      options: [
        { label: "启用", value: "1" },
        { label: "禁用", value: "2" }
      ],
      hallFunConfItem: [
        "支付配置",
        "线下支付",
        "支付页签控制",
        "排行榜管理",
        "vip系统",
        "背景音乐设置",
        "app热更配置",
        "默认头像上传",
        "大厅宣传页配置",
        "底部菜单配置",
        "功能排序设置",
        "公告配置",
        "跑马灯配置",
        "邮件系统"
      ],
      operationRepItem: ["用户团队报表", "游戏报表", "平台报表", "出入款报表"],
      channelPackageManItem: [
        "渠道统计",
        "渠道管理",
        "渠道盈亏数据",
        "渠道支付设置"
      ],
      agentDataItem: ["代理层级", "全民代理列表", "业绩返佣", "业绩返佣列表"],
      dataAnaylzeItem: ["在线在玩", "留存分析", "代理日新增收益"],
      activeOperationItem: [
        "充值优惠",
        "每日签到",
        "抽奖设置",
        "红包雨活动",
        "救援金活动",
        "投注返水活动",
        "活动领取记录"
      ],
      systemConfItem: ["推送功能", "属性配置", "推广配置"],
      powerManItem: ["管理员角色", "管理员列表", "系统操作日志"],
      pageInfo: new PageInfo(4, [5, 10, 15], 4),
      //新增、编辑数据
      dialogTitleType: "",
      dialogFormVisible: false,
      dialogVisible: false,
      labelWidth: "70px",
      dataForm: {
        tier: "", //id
        hierarchy_name: "", //层级名称
        alias: "" //别名
      },
      form: {
        role_name: "",
        role_state: "",
        role_status: ""
      },
      form2: {
        subPackageSet: ["添加"],
        name: "",
        status: "1"
      }
    };
  },
  methods: {
    handleCheckAllChange(val) {
      this.form2.subPackageSet = val ? select : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.operation.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.operation.length;
    },
    search() {},
    //新增代理层
    handelAddClick() {
      this.dataForm.tier = "";
      this.dataForm.hierarchy_name = "";
      this.dataForm.alias = "";
      this.dialogTitleType = "新增代理分层";
      this.dialogFormVisible = true;
    },
    //修改代理层
    handleClick(btn, row) {
      if (btn.type === "edit") {
        this.dialogTitleType = "修改代理分层";
        this.dialogVisible = true;
        this.dataForm.hierarchy_name = row.tier_name;
        this.dataForm.alias = row.tier_alias;
        this.dataForm.tier = row.tier;
      }
    },
    //代理层级
    getList() {
      tierHandler.list().promise.then(res => {
        if (Number(res.code) === 200) {
          // cache = res.data;
          this.records = res.data;
          storage.set("list", this.records);
        }
        //数据处理
        this.records.map(item => {
          item.action = [{ label: "修改", type: "edit" }];
        });
      });
    },
    /**增加代理分层*/
    addLevel() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          if (!this.dataForm.tier) {
            let data = {
              tier_name: this.dataForm.hierarchy_name,
              tier_alias: this.dataForm.alias
            };
            tierHandler
              .add(data)
              .promise.then(res => {
                if (Number(res.code) === 200) {
                  this.$message.success(res.msg);
                }
                this.dialogVisible = false;
                this.getList();
                this.$refs["dataForm"].resetFields(); // 失效
              })
              .catch(e => {
                // 打印一下错误
                console.log(e);
              });
          } else {
            //修改代理分层
            let data = {
              tier: this.dataForm.tier,
              tier_name: this.dataForm.hierarchy_name,
              tier_alias: this.dataForm.alias
            };
            tierHandler
              .set(data)
              .promise.then(res => {
                if (Number(res.code) === 200) {
                  this.$message.success(res.msg);
                }
                this.dialogVisible = false;
                this.$refs["dataForm"].resetFields(); // 失效
                this.getList();
              })
              .catch(e => {
                console.log(e);
              });
          }
        }
      });
    }
  },
  mounted() {
    this.getList();
  },
  watch: {}
};



